.main {
    height: auto; 
    width: 100%;
    padding: 2rem;
  }

  .Project__title{
    font-size: 4rem;
    position: relative;
    height: 10rem;
  }

  .Main__titles{
    font-size: 4rem;
    position: relative;
    height: 16rem;
    gap: 2rem;
    margin-top: 3rem;
  }

  .Main__titles__work{
    font-size: 4rem;
    position: relative;
    gap: 2rem;
    margin-top: 3rem;
  }
  
  .Section__wrapper{
    display: flex;
    list-style: none;
    flex-wrap: wrap;
    width: 100%;
  }

  .kombij-container{
    margin-top: 10rem;
  }

  @media screen and (max-width: 62.5rem) {

  
    .Project__title{
      font-size: 4rem;
      position: relative;
      height: 7rem;
      text-align: center;
    }

    .Main__titles{
      font-size: 4rem;
      position: relative;
   
      height: 10rem;
      text-align: center;
    }

    .Main__titles__work{
      text-align: center;
      position: relative;
      top: 5rem;
    }

    .Section__wrapper{
      display: flex;
      justify-content: center;
      align-items: center;
      list-style: none;
      flex-wrap: wrap;
      width: 100%;
      flex-direction: column-reverse;
    }
  

  }